/* App.module.css */

/* 전체 컨테이너 스타일 */
.container {
    text-align: center;
    padding: 20px;
    font-family: Arial, sans-serif;
}

/* 제목 스타일 */
.title {
    font-size: 2rem;
    color: #333;
    margin-bottom: 20px;
}

/* 로딩 메시지 스타일 */
.loading {
    font-size: 1.5rem;
    color: #007BFF;
}

/* 코인 리스트 스타일 */
.coinsList {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    padding: 0;
}

/* 코인 항목 스타일 */
.coinItem {
    background-color: #f8f8f8;
    border: 1px solid #ddd;
    margin: 10px;
    padding: 15px;
    border-radius: 8px;
    transition: background-color 0.3s ease;
    width: 200px; /* 각 코인 항목의 너비를 고정 */
}

.coinItem:hover {
    background-color: #e2e2e2;
}

.coinName {
    font-weight: bold;
}

.coinSymbol {
    color: #777;
}

.coinPrice {
    color: #28a745;
    font-size: 1.2rem;
}

input[type="text"] {
    padding: 10px;
    font-size: 1rem;
    margin-right: 10px;
    border: 2px solid #ddd;
    border-radius: 5px;
    width: 300px;
}

.buttonGroup {
    display: flex;
    gap: 14px;
    margin-top: 10px;
}

button {
    padding: 10px 15px;
    font-size: 1rem;
    border: none;
    background-color: #007bff;
    color: white;
    border-radius: 10px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

button:hover {
    background-color: #0056b3;
}

